<template>
  <v-card>
    <v-card-title>
      <v-text-field
        v-model="search"
        append-icon="mdi-magnify"
        label="Search"
        single-line
        hide-details
      ></v-text-field>
      <v-select
        v-model="sortBy"
        flat
        solo-inverted
        hide-details
        :items="keys"
        prepend-inner-icon="mdi-magnify"
        label="Sort by"
      ></v-select>
      <v-text-field
        v-model="textSearch"
        type="String"
        label="Less than"
      ></v-text-field>
    </v-card-title>
    <v-data-table
      :headers="headers"
      :items="datas"
      :search="textSearch"
    >
    <!-- <template v-slot:item.user="{ item }">
      <v-chip
        :color="getColor(item.date)"
      >
        {{ item.user }}
      </v-chip>
    </template> -->
    </v-data-table>
  </v-card>
</template>

<script>
  import moment from 'moment'
  export default {
    data () {
      return {
        search: '',
        textSearch: '',
        todayDate: '',
        headers: [
          {
            text: '사용자',
            align: 'start',
            filterable: false,
            value: 'user',
          },
          { text: '내용', value: 'contents' },
          { text: '날짜', value: 'date' },
        ],
        datas: [],
        keys: [
          'user',
          'contents',
          'date',
        ],
      }
    },
    created () {
      this.$axios.post('/api/notice', { ok: 'ok' }).then(res => {
        for (let i = 0; i < res.data.length; i++) {
          // console.log(res.data[i])
          this.datas.push(res.data[i])
          this.todayDate = moment().format('YYYY-MM-DD')
          console.log(this.datas)
        }
      })
    },
    methods: {
      getColor (aaa) {
        if (aaa === this.todayDate) return 'green'
        else return 'white'
      },
    },
  }
</script>
